<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process178"></a>

<p><br></p>
<p><span class="rvts17">像对待牲口一样（</span><span class="rvts50">Treated Like Cattle</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:344,Y:233</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：格洛林（</span><span class="rvts20">Geraldine</span><span class="rvts21">）、梅博（</span><span class="rvts20">Mabel</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">把格洛林和梅博变回人型。</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1187.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在离巫女房间不远处的一个农场里，有两只奶牛。他们由于受到了巫女爱丽丝的诅咒，无法变回人形。</span>
<br><span class="rvts21">女巫师房门的钥匙就在他的房子旁边的一座废弃房屋里（在一块很大的石头上）。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1188.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">进入女巫的房间，会遇到一个秘密地下通道，至少需要</span><span class="rvts20">4</span><span class="rvts21">级的偷窃技能才可以开锁。（如果可以开锁，那么可以暂时免去与爱丽丝的打斗场景）</span>
<br><span class="rvts21">如果偷窃技能较低，可以找到女巫爱丽丝，打败她，从她身上找到地下室的钥匙。但是爱丽丝是一个非常凶悍的死灵巫师，强烈建议把队伍分散开来，与她进行打斗，并且建议队伍至少</span><span class="rvts20">15</span><span class="rvts21">级以上。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1189.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">进入房间后，记得翻阅里面的相关书籍，查看所有的药水制造配方。</span>
<br><span class="rvts21">密室里面有一处充满了自爆老鼠的密室，建议使用不死角色，先把密室中的所有老鼠洞给堵上。</span>
<br><span class="rvts21">之后，找到密室中的一处机关，可以打开附近一扇门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1190.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在爱丽丝的办公桌附近，可以找到一个巫师的眼睛，并找到一瓶</span><span class="rvts20"></span><span class="rvts21">女巫的药水</span><span class="rvts20"></span><span class="rvts21">。此时我们手中拥有了一瓶女巫药水，可以把农场中的一只奶牛变回人形。</span>
<br><span class="rvts21">接下来，我们需要找另外一瓶药水。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1191.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">杀掉女巫困在监狱里的青蛙扎普特（</span><span class="rvts20">Zppt</span><span class="rvts21">），可以从它身上找到女巫药水的制作公式。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1192.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">使用增强草（</span><span class="rvts20">Augmentor</span><span class="rvts21">）</span><span class="rvts20">+</span><span class="rvts21">牛肝菌（</span><span class="rvts20">Boletus</span><span class="rvts21">）</span><span class="rvts20">+</span><span class="rvts21">女巫的眼睛（</span><span class="rvts20">Witch's Eye</span><span class="rvts21">）可以制作出另外一瓶女巫药水。</span>
<br><span class="rvts21">注意，女巫的眼睛就在女巫的屋子里或地下室里，而且只有</span><span class="rvts20">1</span><span class="rvts21">个。使用女巫药水可以给另外一只牛解除诅咒。</span>
<br><span class="rvts21">如果不小心喝下了女巫药水，那么该任务将永远无法完成，并且使用的角色将永久的变成一只牛。让队友杀掉，可以解除诅咒，然而，但该任务将会失败。一旦成功拥有两瓶</span><span class="rvts20"></span><span class="rvts21">女巫的药水</span><span class="rvts20"></span><span class="rvts21">，便可以让两头奶牛服用。</span>
<br><span class="rvts21">注意，让他们服用药水，可以提前向他们索取酬劳。梅博（</span><span class="rvts20">Mabel</span><span class="rvts21">）可以给你一本免费的配方书，格洛林（</span><span class="rvts20">Geraldine</span><span class="rvts21">）可以告诉你他储藏箱的位置，之后两个人都会在浮木城的广场出现。如果拒绝索取酬劳，那么将会获得额外的经验值。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



